
































































































































// @ is an alias to /src
import { Component, Vue } from "vue-property-decorator";
@Component({
    components: {}
})
export default class Vip extends Vue {
    private fixed: boolean = true;
    private value: Number = 25;
    onClickLeft() {
        this.$router.back();
    }

    private onClickRight() {
        this.$router.push({name: 'User'});
    }
}
